header {
    background: #f3f3f5;
    padding-top: 23px;
    padding-bottom: 23px;
    color: #676767;
    font-size: 14px;
    box-shadow: 0 0 15px rgba(0,0,0,0.25);
    font-family: 'Roboto', sans-serif;
    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width:920px) {
            flex-wrap: wrap;
            .logo {
                width: 100%;
                text-align: center;
            }
            & > div {
                margin: 10px 0;
            }
        }
        & > div {
            margin-left: 20px;
            @media (max-width:1050px) {
                &:last-child {
                    display: none;
                }
            }
            @media (max-width:600px) {
                display: none;
            }
        }
    }
    a {
        color: #676767;
        text-decoration: none;
    }
    
}
.block {
    padding-right: 20px;
    border-right: 1px solid #edeef1;
    &_head {
        font-size: 15px;
        color: #252525;
        text-transform: uppercase;
        position: relative;
        margin-bottom: 12px;
        color: #252525;
        font-family: '8483';
        img {
            position: absolute;
            right: calc( 100% + 7px )
        }
    }
}