footer {
    padding: 15px 0;
    background: #252525;
    color: #fff;
    font-family: 'Roboto', sans-serif;
    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width:920px) {
            display: block;  
            .logo,
            & > div {
                display: block;
                max-width: 320px;
                margin: 10px auto;
            }
            & > div {
                padding-left: 10px   
            }
        }
    }
    .block{
        &_head {
            color: #ed132b;
            font-family: '8483';
        }
    }
    a {
        color: #fff;
    }
}