
.exchange {
    &_table {
        text-align: center;
        background: #f3f3f5;
        margin-bottom: 55px;
        font-family: '8483';
        &_head {
            padding: 15px 0;
            text-align: center;
            color: #fff;
            font-weight: 100;
            font-size: 20px;
            background: #252525;
            b {
                color: #ed132b;
            } 
        }
        &_body {
            font-weight: bold;
            &_head {
                border-bottom: 1px solid #dae1e4;
                & > * {
                    padding: 20px 0!important;
                    color: #ed132b;
                    font-size: 15px;
                    & + * {
                        border-left: 1px solid #dae1e4;
                    }
                }
            }
            &_tr {
                font-size: 16px;
                display: flex;
                justify-content: space-between;
                align-items: stretch;
                &:hover {
                    background: #d8d8e2;
                }
                & > * {
                    width: 33.33%;
                    padding: 10px 0;
                    img {
                        display: inline-block;
                        vertical-align: middle;
                    }
                    & + * {
                        border-left: 1px solid #dae1e4;
                    }
                }
            }
        }
        @media (max-width:600px) {
            ~ p + p {
                display: none;
            }
        }
        
    }
    
}

.graf {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 55px;
}

.in_tel {
    text-align: center;
    margin-bottom: 60px;
//    img {
//        width: 100%;
//        max-width: 749px;
//    }
}

.calc {
    background: #252525;
    padding: 50px 25px;
    margin-bottom: 50px;
}
.label {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    position: relative;
    b {
        display: block;
        margin-bottom: 7px;
        color:#ed132b;
        font-size: 15px;
    }
    .plhl {
        position: absolute;
        left: 10px;
        top: 10px;
        color: #676767;
        font-family: 'Roboto', sans-serif;
        i {
            color: #ed132b;
        }
    }
    & textarea,
    & input {
        &.val,
        &:focus {
            & + .plhl {
                opacity: 0!important;
            }
        }
    }
}

.form {
    padding-left: 25px;
    padding-right: 25px;
    .flag {
        min-width: 100%;
    }
    textarea {
        padding: 10px 15px;
        width: 100%;
        min-height: 130px;
        max-height: 130px;
    }
}

button {
    border: 1px solid #fff;
    border-radius: 45px;
    padding: 20px;
    width: 100%;
    text-align: center;
    background: #ed132b;
    color: #fff;
    font-size: 15px;
    font-weight: bold;
    margin-top: 15px;
    &:hover {
        background: #252525;
        border-color: #252525;
    }
}

.copy {
    text-align: center;
    color: #676767;
    font-size: 14px;
    text-align: center;
    background: #151515;
    padding: 10px;
    font-family: 'Roboto', sans-serif;
}
