@import "variables";
@import "mixins";
@import "flex";
@import "reset";
@import "base";
@import "layout";

@import "modules/all";
@import "components/all";

@import "media";

@font-face {
  font-family: '11528';
  src: url('../fonts/11528.eot');
  src: url('../fonts/11528.woff2') format('woff2'),
       url('../fonts/11528.eot?#iefix') format('embedded-opentype');
  src: url('../fonts/CenturyGothic.woff') format('woff'),
       url('../fonts/CenturyGothic.ttf') format('truetype'),
       url('../fonts/CenturyGothic.svg#CenturyGothic') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: '8483';
  src: url('../fonts/8483.eot');
  src: url('../fonts/8483.woff2') format('woff2'),
       url('../fonts/8483.eot?#iefix') format('embedded-opentype');
  src: url('../fonts/CenturyGothic-Bold.woff') format('woff'),
       url('../fonts/CenturyGothic-Bold.ttf') format('truetype'),
       url('../fonts/CenturyGothic-Bold.svg#CenturyGothic-Bold') format('svg');
  font-weight: normal;
  font-style: normal;
}
