@import "variables";
@import "mixins";
html,
body {
	background: #fff;
    font-family: 'Helvetica-Regular';
    font-size: 14px;
}
body {
    &.open {
        overflow: hidden;
    }
}
.wrapper {
    max-width: 1170px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
}
b {
    font-family: '8483';
}
h1 {
    font-size: 36px;
    color: #222222;
    text-align: center;
    position: relative;
    margin-bottom: 60px;
    text-transform: uppercase;
    font-family: '8483';
    b.m_b {
    
        display: none;
        @media (max-width:600px) {
            display: block;
            & + b {
                display: none;
            }
        }
    }
    span {
        font-weight: 100;
        font-family: '11528';
    }
    @media (max-width:600px) {
            font-size: 19px
    }
    &:after {
        content: '';
        display: block;
        position: absolute;
        width: 165px;
        height: 1px;
        background: #dae1e4;
        left: 50%;
        margin-left: -82.5px;
        bottom: -20px;
    }
    &:before {
        content: '';
        display: block;
        position: absolute;
        width: 68px;
        height: 2px;
        background: #ed132b;
        left: 50%;
        margin-left: -34px;
        bottom: -20px;
    }
}
h2 {
    font-size: 23px;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 50px;
    font-family: '8483';
    span {
        font-weight: 100;
        font-family: '11528';
    }
}

.content {
    width: 100%;
    &:not(.wrapper) {
        padding-top: 60px;
        @media (max-width:600px) {
            padding-top: 20px;
        }
    }
    &.wrapper {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
    }
}
p {
    margin-bottom: 30px;
    color: #676767;
    font-size: 15px;
    line-height: 21px;
    font-family: 'Roboto', sans-serif;
    &.h1_p {
        margin-bottom: 55px;
        @media (max-width:600px) {
            display: none;
        }
    }
}
.flag {
    min-width: 175px;
    font-family: '11528';
    @media (max-width:450px) {
        min-width: 50px;
    }
    &-us .jq-selectbox__select-text,
    &-us {
            background: url(../images/flag/us.jpg) no-repeat 0 7px;
    }
    &-eu .jq-selectbox__select-text,
    &-eu {
            background: url(../images/flag/eu.jpg) no-repeat 0 7px;
    }
    &-pl .jq-selectbox__select-text,
    &-pl {
            background: url(../images/flag/pl.jpg) no-repeat 0 7px;
    }
    &-ua .jq-selectbox__select-text,
    &-ua {
            background: url(../images/flag/ua.jpg) no-repeat 0 7px;
    }
    .jq-selectbox {
        width: 100%;
    }
    .jq-selectbox__select-text {
        padding-left: 50px;
        padding-top: 9px;
        height: 39px;
    }
    .jq-selectbox li {
        padding-left: 50px;
        background-position: 10px 6px;
    }
    & + div {
        margin-left: 15px;
    }
}
input {
    width: 100%;
    padding: 12px 15px;
    border: none;   
    font-family: '11528';
}
aside {
    min-width: 360px;
    width: 360px;
    margin-right: 15px;
    background: #ed132b;
    position: relative;
    transition: all ease 0.4s;
    @media (max-width:1000px) {
        position: fixed;
        z-index: 10;
        width: 360px;
        left: -360px;
        top: 0;
        bottom: 0;
        &.open {
            left: 0;
        }
        .scr {
            overflow: hidden;
            overflow-y: auto;
            height: 100%;
            padding-bottom: 20px;
        }
    }
    @media (max-width:450px) {
        min-width: 269px;
        left: -269px;
        width: 269px;
    }
    
    
}
.mob_btn {
    position: absolute;
    top: 15px;
    left: calc( 100% + 20px );
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #252525;
    padding: 14px 10px;
    display: none;
    @media (max-width:1000px) {
        display: block;
    }
    @media (max-width:450px) {
        left: calc( 100% + 1px);
    }
    
    span {
        display: block;
        width: 30px;
        height: 3px;
        border-radius: 5px;
        background: #fff;
        margin-bottom: 6px;
    }
    &.open {
        span {
            &:first-child {
                margin-bottom: -12px;
                margin-top: 9px;
                transform: rotate(-45deg);
                & + span {
                    opacity: 0;
                }
            }
            &:last-child {
                transform: rotate(45deg);
            }
        }
    }
}



.btn {
    &_telega {
        font-family: '11528';
        font-size: 36px;
        display: block;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        background: url(../images/btn.png) no-repeat 0 0;
        color: #000;
        padding: 15px 0;
        background-size: cover;
        margin-right: 40px;
        position: relative;
        @media (max-width: 450px) {
            font-size: 28px;
        }
        img {
            width: auto;
            display: inline-block;
            vertical-align: middle;
            margin-left: 15px;
            transition: all ease 0.4s;
            @media (max-width: 450px) {
                display: none;
            }
        }
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: -1px;
            right: -35px;
            width: 77px;
            height: 77px;
            background: url(../images/btn_t.png) no-repeat center;
            border-radius: 50%;
            border: 10px solid #fefefe;
            box-shadow: 0 0 4px rgba(0,0,0,0.1);
            transition: all ease 0.4s;
        }
        &:hover {
            img {
                margin-left: 35px;
            }
            &:after {
                transform: rotate(-360deg);
            }
        }
    }
}
